.hero {
  @extend %full-screen;
#bg-hero {
  @extend %full-screen;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-position:50% 50%;
  background-repeat:no-repeat;
  background-size: cover;
}

.hero-sub {
  .intro-text {
    text-align:center;
    .tween {
      span {
        &.blue {}
        &.green {
        &.orange {}
        &.red {}
        }
      }
    }
  }
}
}